var AutoPause = /** @class */ (function () {
    function AutoPause() {
        this.threshold = 0.25;
        this.handlerIntersectionObserver = this.handlerIntersectionObserver.bind(this); // Para redefinir el contexto de this a la clase AutoPause.
        this.handlerVisibilityChange = this.handlerVisibilityChange.bind(this); // Para redefinir el contexto de this a la clase AutoPause.
    }
    AutoPause.prototype.run = function (player) {
        this.player = player;
        var observer = new IntersectionObserver(this.handlerIntersectionObserver, {
            threshold: this.threshold
        }); //Se redefine this arriba con bind ya que acÃ¡ toma como this al objeto IntersectionObserver.
        observer.observe(this.player.media);
        document.addEventListener("visibilitychange", this.handlerVisibilityChange); //Se redefine this arriba con bind ya que acÃ¡ toma como this al objeto addEventListener.
    };
    AutoPause.prototype.handlerIntersectionObserver = function (entries) {
        var entry = entries[0];
        console.log(entry);
        if (entry.isIntersecting) {
            this.player.play();
        }
        else {
            this.player.pause();
        }
    };
    AutoPause.prototype.handlerVisibilityChange = function () {
        document.visibilityState === 'hidden' ? this.player.pause() : this.player.play();
        console.log(document.visibilityState);
    };
    return AutoPause;
}());
export default AutoPause;
